///////////////////////////////////////////////////////////////////////////////////////////////////////
// functions

export const onCreated = () => {
    // play 
    $('.play').hide();
    global.basicTimeline = anime.timeline({
        loop: 1,
        autoplay: false,
        complete: (anime) => {
            $('.play').css({'background-color': 'darkblue'});
        }
    });
};

export const onBuild = () => {
    console.log(".build@click");
    $('.build').css({'background-color': 'lawngreen'});
    $('.play').hide();
    // create an ajax object.
    $.ajax({
        url: "/app" + "/build.json",
        type: "GET",
        dataType: "json",
        success: (data, dataType) => {
            if (data.isError) {
                console.log("application error occurred.");
                return;
            } else {
                console.log("write timeline list complete.");
            }

            //  javaScript  FIXME: 
            $.getScript("http://localhost/animesign/resources/scripts/animesign.timeline.js")
            .done((script, textStatus) => {
                console.log("reload the timeline javascript complete.");

                // 
                buildTimeline(global.basicTimeline);

                // 
                initTimeline();

                $('.build').css({'background-color': 'darkblue'});
                $('.play').show();
             })
             .fail((jqxhr, settings, exception) => {
                console.log("the timeline javascript request error occurred.");
             });
        },
        error: (XMLHttpRequest, textStatus, errorThrown) => {
            console.log("http request error occurred.");
        }
    });
};

export const onPlay = () => {
    console.log(".play@click");
    global.basicTimeline.restart();
    $('.play').css({'background-color': 'magenta'});
};

// 
export const showBalloonText = (text, balloonImgId, balloonTextId, top, left) => {
    if (top == null) {
        top = 200; // TODO: 
    }
    if (left == null) {
        left = 200; // TODO: 
    }
    const off = $(balloonImgId).offset();
    $(balloonTextId).offset({
        top: off.top + (top),
        left: off.left + (left)
    });
    $(balloonTextId).css({opacity: 1});

    // 
    let count = 0;
    let pageMark = false;
    const txtArr = text.split("");
    const txtCount = () => {
        // '\v' 
        if (pageMark === false) {
            // 
            if (txtArr[count] !== '\v') {
                var timer1 = setTimeout(txtCount, 100); // TODO: 
                $(balloonTextId).append(txtArr[count].replace(/\n/, "<br>"));
            }
            // 
            else if (txtArr[count] === '\v') {
                var timer2 = setTimeout(txtCount, 500);
                // set the page mark.
                pageMark = true;
            }
            count++;
        }
        // ()
        else if (pageMark === true) {
            var timer3 = setTimeout(txtCount, 150);
            pageMark = false;
            $(balloonTextId).html('');
        }

        if (count === txtArr.length) {
            clearTimeout(timer1);
            clearTimeout(timer2);
            clearTimeout(timer3);
        }
    };
    txtCount();

};

// 
export const hideBalloonText = (balloonTextId) => {
    $(balloonTextId).html('');
};
